import VueRouter from 'vue-router'

import Classroom from 'components/manage/classroom'
import Exam from 'components/manage/exam'
import ExamCreate from 'components/manage/exam-create'
import ExamImport from 'components/manage/exam-import'
import StudentAttendance from 'components/manage/attendance-student'
import CourseAttendance from 'components/manage/attendance-course'
import Volunteer from 'components/manage/volunteer'
import VolunteerAnalyze from 'components/manage/volunteer-analyze'
import VolunteerCreate from 'components/manage/volunteer-create'
import Message from 'components/manage/message/'
import MessageChat from 'components/manage/message/chat'
import MessageContacts from 'components/manage/message/contacts'


export default new VueRouter({
  mode: 'history',
  base: process.env.NODE_ENV === 'production' ? '/pad/manage/features' : '/static/w/pages/classroom-manage.html',
  routes: [
    { path: '', redirect: '/message/chat' },
    { path: '/classroom', component: Classroom },
    { path: '/exam', component: Exam },
    { path: '/exam/create', component: ExamCreate },
    { path: '/exam/import', component: ExamImport },
    { path: '/attendance/student', component: StudentAttendance },
    { path: '/attendance/course', component: CourseAttendance },
    { path: '/volunteer', component: Volunteer },
    { path: '/volunteer/analyze', component: VolunteerAnalyze },
    { path: '/volunteer/create', component: VolunteerCreate },
    {
      path: '/message',
      component: Message,
      redirect: '/message/chat',
      children: [
        { path: 'chat', component: MessageChat },
        { path: 'contacts', component: MessageContacts },
      ],
    },

  ],
  linkActiveClass: 'active',
})




