<template>
  <div id="messageChat">
    <div class="person">
      <div class="searchContainer">
        <!-- <el-input placeholder="搜索" prefix-icon="el-icon-search" v-model="searchContent" @keyup.enter.native="search">
          <i slot="suffix" class="el-icon-error" @click="searchContent=''" style="line-height: 40px" />
        </el-input> -->
      </div>
      <div class="personItemContainer" ref="personItemContainer" v-on:scroll="handleScroll">
        <div v-if="!sessions.length" class="sessionsLoading">暂无消息</div>
        <template v-else>
          <div class="personItem" v-for="item in sessions " :class="isSelected(item) " :key="item.id" @click="itemClickHandle(item)">
            <div class="head">
              <img :src="item.student.avatar" />
            </div>
            <div>
              <div class="name">{{item.student.name}}</div>
              <div class="lastMessage ">{{item.abstract}}</div>
            </div>
            <div class="unread" v-if="item.unread && item.id !== selectId">{{item.unread}}</div>
          </div>
          <div class="sessionsLoading" v-if="haveMoreSession" ref="sessionLoading">加载中...</div>
        </template>
      </div>
    </div>
    <div class="chat">
      <div class="header">
        <span>{{selectItem && selectItem.student ? selectItem.student.name : ''}}</span>
      </div>
      <template v-if="selectId !== null">
        <div class="tip">聊天内容管理员可见</div>
        <div class="message" v-loading="sending" element-loading-background="rgba(0, 0, 0, 0)">
          <div ref="message">
            <div class="viewHistory" v-if="haveMoreMessage && selectId !== null" @click="getHistory()">查看历史聊天记录</div>
            <div class="chatItem" :class="chatItemStatusJudge(item.isMine,item.failed)" v-for="(item, index) in messages" :key="index">
              <div class="chatTime" v-if="timeConvert(item.time,index).length">
                <span>{{timeConvert(item.time,index)}}</span>
              </div>
              <div class="head">
                <img :src="selectItem.student.avatar" />
              </div>
              <div class="text" :class="item.body.kind === 3 ? 'audioContainer' : ''">
                <div v-if="item.body.kind === 1">{{item.body.text}}</div>
                <div v-if="item.body.kind === 2">
                  <img :src="item.body.imgThumbnailUrl" @click="imgSelectHandle(item.body.imgUrl)" :style="{width:'100%',height:'100%',display:'block'}" @load="imgLoad" />
                </div>
                <div v-if="item.body.kind === 3">
                  <audio controls="controls" :src="item.body.audioUrl"></audio>
                </div>
              </div>
              <div class="head teacherHead">
                {{selectItem.teacher.name}}
              </div>
            </div>
          </div>
        </div>
        <div class="input">
          <div v-if="!enableMessage" class="disable-mask">学校管理员已禁用聊天功能</div>
          <div class="operationBar">
            <el-upload action="" :before-upload="sendHandle" :show-file-list="false" name="image" accept="image/*">
              <i class="el-icon-picture"></i>
            </el-upload>
            <el-button size="small" type="primary" @click="sendHandle">发送</el-button>
          </div>
          <textarea :placeholder="enableMessage?'请输入内容':''" :disabled="!enableMessage" v-model="text" ref="textareaRef" @keypress.enter.prevent="sendHandle"></textarea>
        </div>
      </template>
    </div>
    <el-dialog width="min-content" :visible="imgDialog.visible" @close="imgDialog.visible=false" :style="{'minWidth':'100px','minHeight':'50px'}">
      <img :src="imgDialog.url" width="100%" />
    </el-dialog>
  </div>
</template>

<script>
import { get as GET, put as PUT, post as POST } from 'axios'
import { mapState } from 'vuex'
import dayjs from 'dayjs'

const qs = require('qs')

export default {
  data() {
    return {
      messages: [],
      haveMoreMessage: true,
      haveHistory: false,
      searchContent: '',
      selectItem: {},
      selectId: null,
      text: '',
      sessions: [],
      haveMoreSession: true,
      loadingSession: false,
      imgDialog: {
        visible: false,
        url: '',
      },
      sending: false,
      heightStamp: 0,
    }
  },
  methods: {
    chatItemStatusJudge(isMine, failed) {
      const identity = isMine ? 'mine' : 'other'
      const status = failed ? ' failed' : ''
      return identity + status
    },
    isSelected(item) {
      return (this.selectItem || {}).id === item.id ? 'selectd' : ''
    },
    imgSelectHandle(url) {
      this.imgDialog = {
        visible: true,
        url,
      }
    },
    resetStatus(scroll = true) {
      this.text = ''
      const { textareaRef, message } = this.$refs
      if (textareaRef) textareaRef.focus()
      if (scroll && message) {
        this.$nextTick(() => message.scrollIntoView(false))
      }
    },
    timeConvert(dateStr, index) {
      const date = dayjs(dateStr)
      let formatStr
      if (dayjs().year() - date.year() >= 1) {
        formatStr = 'YYYY-MM-DD HH:mm'
      } else if (dayjs().format('YYYY-MM-DD') === date.format('YYYY-MM-DD')) {
        formatStr = 'HH:mm'
      } else {
        formatStr = 'MM-DD HH:mm'
      }

      if (index === 0) {
        return date.format(formatStr)
      }
      const prevMessage = this.messages[index - 1]
      const { time } = prevMessage
      if (date.diff(dayjs(time), 'minutes') > 1) {
        return date.format(formatStr)
      }
      return ''
    },
    imgLoad() {
      if (!this.haveHistory) {
        this.$refs.message.scrollIntoView(false)
      }
    },
    handleScroll() {
      if (this.haveMoreSession) {
        const { personItemContainer, sessionLoading } = this.$refs
        const { scrollHeight, scrollTop } = personItemContainer
        const { height: personItemContainerHeight } = window.getComputedStyle(personItemContainer)
        const { height: loadingHeight } = window.getComputedStyle(sessionLoading)
        // 是否滚动到底部
        const arrived = scrollHeight - scrollTop - parseInt(personItemContainerHeight) <= parseInt(loadingHeight)
        // 是否允许触发(防止二次触发)
        const allow = this.heightStamp === 0 ? true : scrollTop > this.heightStamp + parseInt(loadingHeight)
        if (arrived && allow) {
          this.heightStamp = scrollTop
          this.getSessions(this.sessions.length)
        }
      }
    },
    getHistory() {
      GET(`/teacher/api/chats/${this.selectItem.id}`, {
        params: { start: this.messages.length, limit: 20 },
        headers: { Authorization: this.accessToken },
      }).then(({ data }) => {
        this.haveHistory = true
        if (data.messages && data.messages.length) {
          this.messages = data.messages.concat(this.messages)
          this.$nextTick(() => this.resetStatus(false))
          if (data.messages.length < 20) {
            this.haveMoreMessage = false
          }
        } else {
          this.haveMoreMessage = false
        }
      }).catch(() => {
        this.$message({
          type: 'error',
          message: '请求消息列表失败',
        })
      })
    },
    getSessions(start = 0) {
      if (this.loadingSession) return
      this.loadingSession = true
      const LIMIT = 20
      GET('/teacher/api/chats/', {
        params: { start, limit: LIMIT },
        headers: { Authorization: this.accessToken },
      }).then(({ data }) => {
        if (data.sessions === undefined) throw new Error()
        if (this.sessions.length === 0 && data.sessions.length) {
          this.selectItem = data.sessions[0]
          this.selectId = data.sessions[0].id
        } else {
          this.selectItem = data.sessions.find(ele => ele.id === this.selectId)
        }
        if (start === 0) {
          if (this.sessions.length === 0) this.haveMoreSession = data.sessions.length === LIMIT
          this.sessions = data.sessions
            .concat(this.sessions)
            .filter((ele, index, arr) => arr.findIndex((e) => ele.id === e.id) === index)
        } else {
          this.sessions = this.sessions.concat(data.sessions)
          this.haveMoreSession = data.sessions.length === LIMIT
        }
      }).catch(() => {
        this.$message({
          type: 'error',
          message: '获取消息列表失败',
        })
      }).finally(() => {
        this.loadingSession = false
      })
    },
    getMessages(limit) {
      this.markRead()
      GET(`/teacher/api/chats/${this.selectItem.id}`, {
        params: { start: 0, limit },
        headers: { Authorization: this.accessToken },
      }).then(({ data }) => {
        if (data.messages && data.messages.length) {
          this.messages = this.messages.concat(data.messages)
          this.$nextTick(() => this.resetStatus(true))
        }
      }).catch(() => {
        this.$message({
          type: 'error',
          message: '请求消息列表失败',
        })
      })
    },
    itemClickHandle(item) {
      if (item.id !== this.selectId) {
        this.selectItem = item
        this.selectId = item.id
        this.haveMoreMessage = true
        this.haveHistory = false
        this.messages = []
        this.resetStatus()
      }
    },
    markRead() {
      if (this.selectItem.unread > 0) {
        PUT(`/teacher/api/chats/${this.selectItem.id}`, {}, {
          headers: { Authorization: this.accessToken },
        })
        this.sessions = this.sessions.map(ele => (ele.id === this.selectId ? { ...ele, unread: 0 } : ele))
      }
    },
    search() {
      console.log(this.searchContent)
    },
    sendHandle(input) {
      if (this.selectId === null) return
      let headers = { Authorization: this.accessToken }
      let params
      if (input.toString().match(/File\]$/)) {
        this.sending = true
        params = new FormData()
        params.append('image', input, input.name)
        params.append('kind', 2)
        headers = { ...headers, 'Content-Type': 'multipart/form-data' }
      } else {
        if (!this.text.length) return
        params = qs.stringify({
          kind: 1, text: this.text,
        })
      }
      POST(this.sendMessageUrl, params, { headers })
        .then(({ data }) => {
          if (data.message) {
            this.messages = this.messages.concat(data.message)
          } else {
            throw new Error()
          }
        }).catch(() => {
          this.$message({
            type: 'error',
            message: '消息发送失败',
          })
        }).finally(() => {
          this.sending = false
          this.$nextTick(() => this.resetStatus())
        })
      return false
    },
  },
  async mounted() {
    this.resetStatus()
    const { id } = this.$route.query
    if (id !== undefined) {
      try {
        await POST('/teacher/api/chats/create', qs.stringify({
          student: id,
        }), { headers: { Authorization: this.accessToken } })
      } catch (err) {
        this.$message({
          type: 'error',
          message: '创建会话失败',
        })
      }
    }
    this.getSessions()
    this.timeHandle = window.setInterval(() => {
      this.getSessions()
    }, 10000)
  },
  computed: {
    sendMessageUrl() {
      const { id = 0 } = this.selectItem
      return `/teacher/api/chats/${id}`
    },
    ...mapState({
      teacher: 'teacher',
      accessToken: 'accessToken',
      enableMessage: 'enableMessage',
    }),
  },
  watch: {
    selectId(val) {
      const selectItem = this.sessions.find(ele => ele.id === val)
      const limit = selectItem.unread
      if (selectItem && limit) {
        this.getMessages(limit)
      }
    },
    sessions(val) {
      const findResult = val.find(ele => ele.id === this.selectId)
      if (findResult && findResult.unread) {
        this.getMessages(findResult.unread)
      }
    },
  },
  destroyed() {
    window.clearInterval(this.timeHandle)
  },
}
</script>

<style lang="less">
#messageChat {
  height: 100%;
  width: 100%;
  display: flex;
  font-size: 15px;
  .searchContainer {
    width: 100%;
    height: 60px;
    padding: 10px;
    border-bottom: 1px solid #ddd;
  }
  .person {
    height: 100%;
    border: 1px solid #ddd;
    width: 250px;
    flex-shrink: 0;
    .personItemContainer {
      position: relative;
      width: 100%;
      overflow-y: scroll;
      height: calc(~"100% - 60px");
      &::-webkit-scrollbar {
        display: none;
      }
    }
    .personItem {
      padding: 10px;
      background: #fff;
      display: flex;
      align-items: center;
      width: 100%;
      position: relative;
      cursor: pointer;
      .unread {
        line-height: 20px;
        border-radius: 88px;
        background: red;
        color: #fff;
        padding: 0 5px;
        height: 20px;
        position: absolute;
        right: 10px;
        top: 50%;
        margin-top: -10px;
      }
      .head {
        width: 50px;
        height: 50px;
        overflow: hidden;
        border-radius: 50%;
        margin-right: 5px;
        flex-shrink: 0;
        flex-grow: 0;
        + div {
          width: calc(~"100% - 75px");
        }
        img {
          width: 100%;
          height: 100%;
        }
      }
      .name {
        font-size: 14px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
      .lastMessage {
        .name;
        font-size: 12px;
      }
    }
    .selectd {
      background: #ddd;
    }
  }
  .chat {
    position: relative;
    .person;
    border-left: none;
    width: ~"calc(100% - 250px)";
    .header {
      font-size: 18px;
      height: 60px;
      border-bottom: 1px solid #ddd;
      display: flex;
      align-items: center;
      padding: 10px 20px;
    }
    .tip {
      position: absolute;
      top: 60px;
      left: 0;
      font-size: 14px;
      height: 20px;
      line-height: 20px;
      width: 100%;
      text-align: center;
      background: #eee;
      z-index: 10;
    }
    .message {
      padding-top: 20px;
      height: calc(~"70% - 60px");
      display: flex;
      flex-direction: column;
      overflow-y: scroll;
      overflow-x: hidden;
      // &::-webkit-scrollbar {
      //   display: none;
      // }
      .viewHistory {
        color: #66ccff;
        text-align: center;
        padding: 20px 10px 10px;
        text-decoration: underline;
        cursor: pointer;
      }

      .chatItem {
        width: 100%;
        display: flex;
        padding: 10px 5px;
        padding-bottom: 0;
        flex-shrink: 0;
        flex-grow: 0;
        flex-wrap: wrap;
        &:last-child {
          padding-bottom: 10px;
        }
        .chatTime {
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          > span {
            display: inline-block;
            margin-bottom: 10px;
            padding: 2px 8px;
            border-radius: 8px;
            background: #ddd;
            font-size: 13px;
          }
        }
        .teacherHead {
          display: flex;
          align-items: center;
          font-weight: bold;
          justify-content: center;
          white-space: nowrap;
          background: #66ccff;
          color: #fff;
        }

        .head {
          width: 40px;
          height: 40px;
          overflow: hidden;
          border-radius: 50%;
          margin: 0 5px;
          flex-shrink: 0;
          flex-grow: 0;
          img {
            height: 100%;
            width: 100%;
          }
        }
        .text {
          max-width: calc(~"100% - 130px");
          background: #ddd;
          overflow-wrap: break-word;
          word-break: break-all;
          // white-space: pre-wrap;
          padding: 10px;
          border-radius: 5px;
          line-height: 20px;
          position: relative;
        }
      }
      .failed {
        div {
          &:nth-child(2) {
            background: #f56c6c;
          }
        }
      }
      .other {
        justify-content: flex-start;
        .head {
          margin-right: 10px;
          &:last-child {
            visibility: hidden;
          }
        }
        .text {
          &::before {
            content: "";
            display: block;
            border-right: 10px solid #ddd;
            border-top: 5px solid transparent;
            border-bottom: 5px solid transparent;
            position: absolute;
            top: 15px;
            left: -10px;
          }
        }
      }
      .mine {
        justify-content: flex-end;
        .head {
          margin-left: 10px;
          &:first-child,
          &:nth-child(2) {
            visibility: hidden;
          }
        }
        .text {
          &::after {
            content: "";
            display: block;
            border-left: 10px solid #ddd;
            border-top: 5px solid transparent;
            border-bottom: 5px solid transparent;
            position: absolute;
            top: 15px;
            right: -10px;
          }
        }
      }
    }
    .input {
      border-top: 1px solid #ddd;
      height: 30%;
      display: flex;
      flex-direction: column;
      position: relative;
      .disable-mask {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.3);
        z-index: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: not-allowed;
        user-select: none;
      }
      .operationBar {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        padding: 5px;
        padding-right: 10px;
        > * {
          margin-left: 10px;
          > i {
            font-size: 30px;
          }
        }
        .el-button {
          padding-top: 5px;
          padding-bottom: 5px;
        }
      }
      textarea {
        font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB,
          Microsoft YaHei, Arial, sans-serif;
        width: 100%;
        font-size: 15px;
        flex-grow: 1;
        cursor: text;
        height: 100%;
        resize: none;
        outline: none;
        border: none;
        overflow-wrap: break-word;
        word-break: break-all;
        padding: 10px;
        &:focus {
          outline: none;
        }
        &:disabled {
          background: #fff;
        }
      }
    }
  }
  .el-upload {
    font-size: 25px;
    line-height: 0;
  }
  .el-dialog {
    width: fit-content !important;
  }
  .el-dialog__body {
    max-width: 100%;
    max-height: 100%;
  }
  .audioContainer {
    line-height: 0px !important;
    padding: 5px !important;
  }
  .sessionsLoading {
    text-align: center;
    height: 40px;
    line-height: 40px;
    background: #eee;
  }
  .el-loading-mask {
    pointer-events: none;
  }
}
</style>



