<template>
  <el-card>
    <main-header slot="header" title="课程考勤详情" />
    <div class="container">
      <div>
        <span>选择日期</span>
        <el-date-picker @change="datePickerChange" v-model="selectDate" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期" unlink-panels :picker-options="pickerOptions" />
      </div>
      <el-input size="medium" placeholder="搜索学生姓名" v-model="searchContent" style="width:200px">
        <el-button slot="append" icon="el-icon-search" @click="searchHandle" />
      </el-input>
    </div>
    <el-table :data="courseInfo">
      <el-table-column align="center" v-for="(item, index) in columns" :key="index" v-bind="item" />
      <el-table-column align="center" label="操作">
        <template slot-scope="{row}">
          <el-button type="text" @click="detail(row)">查看详情</el-button>
        </template>
      </el-table-column>
    </el-table>
    <detail-dialog v-bind="detailDialogData" @close="detailDialogData.visible = false" />
  </el-card>
</template>

<script>
import { pickerOptions } from './config'
import DetailDialog from './dialog/attendance-course-detail'

const labels = ['课程名称', '老师', '应考勤', '考勤率', '正常', '缺席', '迟到', '请假']
const props = ['name', 'teacher', 'num', 'rate', 'normol', 'absent', 'late', 'leave']
const columns = labels.map((ele, index) => ({
  label: ele,
  prop: props[index],
}))

export default {
  components: { DetailDialog },
  data() {
    return {
      courseInfo: [],
      selectDate: [],
      searchContent: '',
      columns,
      pickerOptions,
      detailDialogData: {
        visible: false,
        data: {},
      },
    }
  },
  methods: {
    datePickerChange(val) {
      console.log(val)
    },
    searchHandle() {
      console.log(this.searchContent.trim())
    },
    detail(row) {
      console.log(row)
      this.detailDialogData = {
        visible: true,
        data: row,
      }
    },
  },
  mounted() {
    this.courseInfo = [{
      name: '课程1',
    }]
  },
}
</script>

<style lang="less" scoped>
.container {
  display: flex;
  align-items: center;
  padding: 20px;
  justify-content: space-between;
  width: 100%;
}
.el-table {
  .el-button--text {
    padding: 0;
  }
}
</style>




