<template>
  <div>
    <el-card>
      <main-header slot="header" title="成绩管理">
        <el-button slot="right" type="primary" @click="createExam()">新建考试</el-button>
      </main-header>
      <div class="table-before">
        <div>
          <label>状态：</label>
          <el-select v-model="selectValue" :value="0" size="small" @change="selectHandle">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
        </div>
        <el-input placeholder="搜索考试名称" v-model="searchContent" size="small" style="width:200px">
          <el-button slot="append" icon="el-icon-search" @click="searchHandle" />
        </el-input>
      </div>
      <el-table :data="dataSource" stripe style="width: 100%">
        <el-table-column prop="id" label="ID" align="center" />
        <el-table-column prop="name" label="考试名称" align="center">
          <template slot-scope="{row}">
            <el-input size="small" v-model="examName" v-if="selectId === row.id"></el-input>
            <el-button v-else-if="row.status === '公布中'" type="text" @click="classClickHandle(row)">{{row.name}}</el-button>
            <span v-else>{{row.name}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="class" label="涉及班级" align="center" />
        <el-table-column prop="status" label="状态" align="center" />
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button type="text" v-if="statusJudge(scope,'未公布')">公布</el-button>
            <el-button type="text" v-if="statusJudge(scope,'公布中')">结束</el-button>
            <el-button type="text" v-if="statusJudge(scope,'未公布','公布中')" @click="createExam(scope)">{{selectId === scope.row.id?'完成':'编辑'}}</el-button>
            <el-button type="text" v-if="statusJudge(scope,'未公布','公布中','已结束')">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination layout="prev, pager, next, jumper" :total="total" :page-size="20" @current-change="pageChangeHandle" />
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      visible: false,
      selectId: undefined,
      examName: '',

      total: 30,

      selectValue: 0,
      searchContent: '',
      options: [
        {
          value: 0,
          label: '未公布',
        },
        {
          value: 1,
          label: '公布中',
        },
        {
          value: 2,
          label: '已结束',
        },
      ],
      dataSource: [
        {
          id: 920,
          name: '期中考试',
          class: '14个班',
          status: '已结束',
        },
        {
          id: 900,
          name: '期中考试',
          class: '14个班',
          status: '未公布',
        },
        {
          id: 980,
          name: '期中考试',
          class: '14个班',
          status: '公布中',
        },
        {
          id: 981,
          name: '期中考试',
          class: '14个班',
          status: '公布中',
        },
      ],
    }
  },

  methods: {
    createExam(scope) {
      if (scope && scope.row.status === '公布中') {
        console.log('公布中')
        const { row: { id, name } } = scope
        if (this.selectId === id) {
          console.log(this.examName)
          //update
          this.selectId = undefined
          this.examName = ''
        } else {
          this.selectId = id
          this.examName = name
        }
        return
      }
      const routeInfo = { path: '/exam/create' }
      console.log(scope)
      if (scope) {
        routeInfo.query = { id: scope.row.id }
      }
      this.$router.push(routeInfo)
    },
    searchHandle() {
      console.log(this.searchContent)
    },
    statusJudge({ row }, ...status) {
      return status.indexOf(row.status) !== -1
    },
    selectHandle(val) {
      console.log(val)
    },
    pageChangeHandle(val) {
      console.log(val)
    },
    classClickHandle({ id, name }) {
      console.log(id, name)
      this.$router.push({
        path: '/exam/import',
        query: { id, name },
      })
    },
  },
}
</script>

<style lang="less" scoped>
.el-pagination {
  margin-top: 20px;
}
.el-table {
  td {
    .cell {
      button {
        padding: 0;
      }
    }
  }
}
.table-before {
  > div {
    flex-grow: 0;
    flex-shrink: 0;
  }
}
</style>



