<template>
  <el-card>
    <main-header slot="header" title="学生考勤详情" />
    <div class="container">
      <div>
        <span>选择日期</span>
        <el-date-picker @change="datePickerChange" v-model="selectDate" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期" unlink-panels :picker-options="pickerOptions" />
      </div>
      <el-input size="medium" placeholder="搜索学生姓名" v-model="searchContent" style="width:200px">
        <el-button slot="append" icon="el-icon-search" @click="searchHandle" />
      </el-input>
    </div>
    <div class="container" :style="{justifyContent:'space-around',padding: '20px 0 0'}">
      <div ref="chartContainer" :style="{width:'200px',height:'200px'}" />
      <div>
        <div v-for="({label,num=0,percent=0,key:className,color}, key) in attendanceInfo" :key="key" class="percent">
          <div>{{label}}</div>
          <el-progress :text-inside="true" :stroke-width="15" :percentage="percent" :color="color" :show-text="false">
          </el-progress>
          <div>{{`${num} / ${percent}%`}}</div>
        </div>
      </div>
    </div>
    <div class="container" :style="{padding: '0 0 20px 0'}">
      <span />
      <el-button type="primary" @click="exportAttendance">导出数据</el-button>
    </div>
    <el-table :data="studentInfo" stripe style="width: 100%">
      <el-table-column v-for="({label,prop}, index) in studentInfoColumns" :label="label" :prop="prop" :key="index" align="center" />
      <el-table-column label="操作" align="center">
        <el-button slot-scope="{row}" type="text" @click="detail(row)">
          查看详情
        </el-button>
      </el-table-column>
    </el-table>
    <el-pagination layout="prev, pager, next, jumper" :total="total" :page-size="20" @current-change="pageChangeHandle" />
    <attendance-detail v-bind="detailDialogData" @close="detailDialogData.visible = false"></attendance-detail>
  </el-card>
</template>

<script>
import chart from '@/etc/echarts'
import AttendanceDetail from './dialog/attendance-student-detail'
import { studentInfoColumns, pickerOptions, attendanceInfo, chartOptions } from './config'


export default {
  components: {
    AttendanceDetail,
  },
  data() {
    return {
      selectDate: [],
      studentInfo: [],
      studentInfoColumns,
      searchContent: '',
      pickerOptions,
      total: 1,
      pageIndex: 1,
      attendanceInfo,
      detailDialogData: {
        visible: false,
        data: {},
      },
    }
  },

  methods: {
    datePickerChange(val) {
      console.log(val)
    },
    searchHandle() {
      console.log(this.searchContent.trim())
    },
    pageChangeHandle(val) {
      this.pageIndex = val
      console.log(val)
    },
    detail(row) {
      console.log(row)
      this.detailDialogData = {
        visible: true,
        data: { id: row.id },
      }
    },
    exportAttendance() {
      console.log('export')
    },
  },

  mounted() {
    this.attendanceData = [
      { num: '13', percent: 90 },
      { num: '30', percent: 30 },
      { num: '22', percent: 68 },
      { num: '13', percent: 70 },
    ]
    this.attendanceInfo = this.attendanceInfo.map((ele, index) => ({
      ...ele,
      ...this.attendanceData[index],
    }))
    this.studentInfo = [{
      name: '张三',
      normol: 1,
      late: 1,
      absent: 1,
      leave: 1,
    }, {
      name: '张二',
      normol: 2,
      late: 2,
      absent: 2,
      leave: 2,
    }]
    chart.then(([echarts]) => {
      this.chart = echarts.init(this.$refs.chartContainer)
      this.chart.setOption(chartOptions)
    })
  },
}
</script>

<style lang="less" scoped>
.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0 10%;
}
.percent {
  width: 300px;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  margin: 10px 0;
  > div {
    flex-shrink: 0;
  }
  > div[role='progressbar'] {
    flex-grow: 1;
    margin: 0 20px 0 10px;
  }
}
.el-table {
  .el-button--text {
    padding: 0;
  }
}
</style>



