<template>
  <el-card id="exam-create">
    <main-header slot="header" title="新建志愿调研" />
    <el-form :model="form">
      <el-form-item label="调研名称" required>
        <el-input v-model="form.name" />
      </el-form-item>
      <el-form-item label="开始时间" required>
        <el-date-picker v-model="form.startTime" type="datetime" placeholder="请选择开始时间" />
      </el-form-item>
      <el-form-item label="结束时间" required>
        <el-date-picker v-model="form.endTime" type="datetime" placeholder="请选择结束时间" />
      </el-form-item>
      <el-form-item label="设置调研内容" required>
        <el-radio-group v-model="form.type">
          <el-radio :label="0">6选3</el-radio>
          <el-radio :label="1">7选3</el-radio>
          <el-radio :label="2">自定义</el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <div class="container">
      <el-tag v-for="(item, index) in subjectList" :key="index" closable @close="tagCloseHandle(item)" :disable-transitions="false" :type="typeJudge(item)" @click.native="tagSelectHandle(item)">
        {{item}}
      </el-tag>
      <el-input v-if="inputVisible" v-model="inputValue" ref="addTagInput" @keyup.enter.native="inputConfirmHandle" @blur="inputConfirmHandle" size="small" />
      <el-button v-else type="primary" size="small" @click="showInput">
        添加新科目
      </el-button>
    </div>
    <el-table :row-key="row => `${row.id}`" @filter-change="classFilter" :data="students" stripe style="width: 100%" @selection-change="selectChange">
      <el-table-column type="selection" width="40" reserve-selection/>
      <el-table-column prop="id" label="班级名称" align="center" />
      <el-table-column column-key="grade" prop="grade" :filters="[{text:'高一',value:22}]" label="年级 " align="center" />
      <el-table-column column-key="teacher" prop="teacher" :filters="[{text:'张三',value:22}]" label="班主任 " align="center" />
      <el-table-column prop="number" label="学生数" align="center" />
      <el-table-column :render-header="renderSearchInput" width="220" />
    </el-table>
    <el-pagination layout="prev, pager, next, jumper" :total="1" :page-size="20" @current-change="pageChangeHandle" />
    <el-button type="primary" @click="submitHandle">完成</el-button>
  </el-card>
</template>

<script>
const subjectList = ['语文', '数学', '英语', '物理', '化学', '生物', '地理', '历史', '政治']
export default {
  data() {
    return {
      form: {
        name: null,
        startTime: null,
        endTime: null,
        type: 0,
      },
      subjectList,
      inputVisible: false,
      inputValue: '',
      selectOptions: [],
      students: [{ id: 1 }],
      studentsSelect: [],
      searchContent: '',

      pageIndex: 1,
    }
  },
  methods: {
    typeJudge(item) {
      return this.selectOptions.find(ele => ele === item)
        ? ''
        : 'info'
    },
    tagCloseHandle(tag) {
      this.subjectList = this.subjectList.filter(ele => ele !== tag)
      this.selectOptions = this.selectOptions.filter(ele => ele !== tag)
    },
    tagSelectHandle(item) {
      const isSelect = this.selectOptions.find(ele => ele === item)
      if (isSelect) {
        this.selectOptions = this.selectOptions.filter(ele => ele !== item)
      } else {
        this.selectOptions = this.selectOptions.concat(item)
      }
    },
    showInput() {
      this.inputVisible = true
      this.$nextTick(() => {
        this.$refs.addTagInput.$refs.input.focus()
      })
    },
    resetInputStatus() {
      this.inputVisible = false
      this.inputValue = ''
    },
    inputConfirmHandle() {
      if (this.inputValue.trim().length) {
        this.subjectList = this.subjectList.concat(this.inputValue)
      }
      this.$nextTick(() => this.resetInputStatus())
    },
    selectChange(val) {
      this.studentsSelect = val
    },
    classFilter(val) {
      console.log(val)
    },
    searchHandle() {
      console.log(this.searchContent)
    },
    pageChangeHandle(pageIndex) {
      this.pageIndex = pageIndex
    },
    submitHandle() {
      console.log('submit')
    },
    renderSearchInput(createElement) {
      return createElement(
        'el-input',
        {
          attrs: {
            placeholder: '搜索',
            size: 'small',
          },
          on: {
            input: val => {
              this.searchContent = val
            },
          },
          style: {
            display: 'flex',
            width: '200px',
          },
        },
        [
          createElement('el-button', {
            slot: 'append',
            attrs: { icon: 'el-icon-search' },
            on: { click: this.searchHandle },
          }),
        ],
      )
    },
  },
}
</script>

<style lang="less">
#exam-create {
  .el-form-item {
    label {
      width: 8rem;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  .container {
    display: flex;
    flex-wrap: wrap;
    padding-right: 20%;
    justify-content: space-around;
    width: 100%;
    > * {
      flex-grow: 0;
      flex-shrink: 0;
      margin: 10px;
      width: auto;
      cursor: pointer;
    }
    > .el-input {
      width: 92px;
    }
    &::after {
      content: '';
      flex-grow: 1;
    }
  }
  .el-table {
    margin-top: 20px;
    .el-input {
      display: flex;
      width: 200px;
    }
    .el-input-group__append {
      line-height: initial;
      padding: 0 20px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}
</style>




