<template>
  <div id="contacts">
    <div class="tree">
      <div class="searchContainer">
        <!-- <el-input placeholder="搜索" prefix-icon="el-icon-search">
          <i slot="suffix" class="el-icon-error" @click="searchContent=''" style="line-height: 40px" />
        </el-input> -->
      </div>
      <div>
        <el-tree :props="treeProps" @node-click="treeClickHandle" highlight-current lazy :load="treeLoad" />
      </div>
    </div>
    <div class="studentDetail">
      <div class="header">
        <span v-if="selectedItem">{{selectedItem.name}}</span>
      </div>
      <template v-if="selectedItem">
        <div class="head">
          <div class="baseInfo">
            <span class="name">{{selectedItem.name}}</span>
            <!-- <span>{{selectedItem.gender}}</span> -->
            <br>
            <span class="id">学号:{{selectedItem.id}}</span>
          </div>
          <img :src="selectedItem.avatar" alt="头像" />
        </div>
        <!-- <div class="studentInfo">
          <span>
            <span>行政班:</span>{{selectedItem.className}}</span>
          <span>
            <span>班主任:</span>{{selectedItem.teacher}}</span>
          <span>
            <span>家长电话:</span>{{selectedItem.phone}}</span>
        </div> -->
        <el-button type="primary" @click="sendMessage">发送消息</el-button>
      </template>

    </div>
  </div>
</template>

<script>
import { get as GET } from 'axios'
import { mapState } from 'vuex'

export default {
  data() {
    return {
      courses: [],
      selectIndex: -1,
      treeProps: {
        label: 'name',
        children: 'children',
        isLeaf: 'leaf',
      },
      selectedItem: null,
    }
  },
  computed: {
    ...mapState({ teacher: 'teacher' }),
  },
  methods: {
    treeClickHandle(data) {
      if (data.avatar !== undefined) {
        this.selectedItem = data
      }
    },
    sendMessage() {
      const { avatar, id, name } = this.selectedItem
      this.$router.push({
        path: '/message/chat',
        query: { avatar, id, name },
      })
    },
    treeLoad(node, resolve) {
      const { level, data, parent } = node
      switch (level) {
        case 0:
          resolve([
            { name: '行政班' }, { name: '教学班' },
          ])
          break
        case 1:
          if (data.name === '行政班') {
            resolve(this.teacher.classes.filter(c => c.status === 1))
          }
          if (data.name === '教学班') {
            resolve(this.teacher.courses.filter(c => c.status === 1))
          }
          break
        case 2:
          let url = ''
          let params = {}
          if (parent.data.name === '教学班') {
            url = `/pad/j/courses/${data.id}/students`
          } else {
            url = `/pad/j/management/classes/${data.id}/students`
            params = {
              fields: ['id', 'name', 'avatar'],
              getAll: 1,
            }
          }

          GET(url, { params }).then(({ data: { students } }) => {
            resolve(students.map(ele => ({ ...ele, leaf: true })))
          }).catch(() => {
            this.$message({
              message: '请求学生失败',
              type: 'error',
            })
          })
          break
        default:
          break
      }
    },
  },
}
</script>

<style lang="less">
#contacts {
  .border {
    border: 1px solid #ddd;
  }
  height: 100%;
  width: 100%;
  display: flex;
  .tree {
    .border;
    width: 250px;
    height: 100%;
    flex-shrink: 0;
    .searchContainer {
      width: 100%;
      height: 60px;
      padding: 10px;
      border-bottom: 1px solid #ddd;
      + div {
        overflow-y: scroll;
        height: calc(~'100% - 60px');
      }
    }
  }
  .studentDetail {
    .border;
    // flex-grow: 1;
    min-width: 40vw;
    width: 250px;
    display: flex;
    flex-direction: column;
    border-left: none;
    align-items: center;
    > * {
      width: 100%;
    }
    .header {
      font-size: 18px;
      height: 60px;
      border-bottom: 1px solid #ddd;
      display: flex;
      align-items: center;
      padding: 10px;
    }
    .head {
      > * {
        flex-shrink: 1;
      }
      padding: 50px 25%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .id {
        font-size: 12px;
        color: #666;
      }
      .name {
        font-size: 15px;
      }
      img {
        width: 80px;
        height: 80px;
        border-radius: 50px;
        .border;
      }
    }
    .studentInfo {
      padding: 50px 25%;
      display: flex;
      flex-direction: column;
      span {
        span {
          font-size: 16px;
        }
      }
    }
    button {
      width: 100px;
    }
  }
}
</style>




