<template>
  <el-dialog :visible="visible" title="查看详情">
    <el-table :data="detailData">
      <el-table-column v-for="({label, prop}, index) in columns" :prop="prop" :label="label" :key="index"></el-table-column>
    </el-table>
  </el-dialog>
</template>

<script>
const columns = [
  { label: '课程名称', prop: 'name' },
  { label: '老师', prop: 'teacher' },
  { label: '正常', prop: 'normol' },
  { label: '缺席', prop: 'absent' },
  { label: '迟到', prop: 'late' },
  { label: '请假', prop: 'leave' },
]

export default {
  props: {
    visible: { type: Boolean },
    data: { type: Object },
  },
  data() {
    return {
      detailData: [],
      columns,
    }
  },
  methods: {
    close() {
      this.$emit('close', { type: 'student' })
    },
  },
  mounted() {
    console.log(this.data)
  },
}
</script>



