<template>
  <div>
    <el-card class="box-card">
      <main-header slot="header" title="教室班牌管理">
        <div slot="right">
          <el-select @change="selectHandle" placeholder="批量操作" value="批量操作" class="batch-select">
            <el-option v-for="(value, key) in batchOptions" :key="key" :label="value" :value="key" />
          </el-select>
          <el-button @click="create('add')" type="primary">新建教室</el-button>
        </div>
      </main-header>
      <div class="table-before">
        <div class="defaultPassword">班牌默认密码：{{defaultPassword}}</div>
        <!-- <div>
          <el-input placeholder="搜索教室名称" v-model="searchContent" size="small">
            <el-button slot="append" icon="el-icon-search" @click="searchHandle" />
          </el-input>
        </div> -->
        <div>
          <div>已选中
            <span>&nbsp;{{isSelectAll ? total : selectOptions.length}}&nbsp;</span>
            项
          </div>
          <!-- <el-tooltip content="全选指选中所有教室" placement="top">
            <div>全选
              <el-switch v-model="isSelectAll">
              </el-switch>
            </div>
          </el-tooltip> -->
        </div>
      </div>
      <el-table :row-key="row => `${row.id}`" :data="tableData" stripe style="width: 100%" @selection-change="selectChange">
        <el-table-column type="selection" width="40" reserve-selection />
        <el-table-column prop="id" label="ID" align="center" />
        <el-table-column prop="name" label="教室名称" align="center" />
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button type="text" @click="resetPassword([scope.row])">
              重置密码
            </el-button>
            <el-button type="text" @click="setting([scope.row])">设置</el-button>
            <el-button type="text" @click="create('edit',scope)">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination layout="prev, pager, next, jumper" :total="total" :page-size="20" @current-change="pageChangeHandle" />
    </el-card>
    <classroom-create @confirm="createOrUpdateClassroom" v-bind="createDialogData" @close="close" />
    <classroom-setting v-bind="settingDialogData" @close="close" @confirm="batchSet" />
  </div>
</template>

<script>
import axios from 'axios'
import ClassroomCreate from './dialog/classroom-create'
import ClassroomSetting from './dialog/classroom-setting'

const qs = require('qs')

export default {
  components: {
    ClassroomCreate,
    ClassroomSetting,
  },
  mounted() {
    this.getClassrooms()
  },
  data() {
    return {
      selectIndex: 0,
      isSelectAll: false,
      searchContent: '',
      batchOptions: ['批量重置密码', '批量设置'],
      selectOptions: [],
      createDialogData: null,
      settingDialogData: null,

      total: 0,
      pageIndex: 1,

      tableData: [],
      defaultPassword: '',
    }
  },
  methods: {
    getClassrooms() {
      const url = '/pad/j/classrooms'
      axios.get(url, { params: { page: this.pageIndex } }).then(response => {
        if (response.data.status === 0) {
          this.tableData = response.data.classrooms
          this.total = response.data.total
          this.defaultPassword = response.data.eboardPassword
        }
      })
    },
    selectChange(val) {
      this.selectOptions = val
    },
    createClassroom(data) {
      const url = '/pad/j/classrooms/create'
      axios.post(url, qs.stringify(data)).then(response => {
        if (response.data.status === 0) {
          this.$message({
            type: 'success',
            message: '创建成功',
          })
          this.getClassrooms()
          this.createDialogData.visible = false
        } else {
          this.$message({
            type: 'warning',
            message: '创建失败，教室名字已存在',
          })
        }
      })
    },
    rename(data) {
      const url = `/pad/j/classrooms/${data.id}/update`
      axios.post(url, qs.stringify(data)).then(response => {
        if (response.data.status === 0) {
          this.$message({
            type: 'success',
            message: '修改成功',
          })
          this.getClassrooms()
          this.createDialogData.visible = false
        } else {
          this.$message({
            type: 'warning',
            message: '修改失败，教室名字已存在',
          })
        }
      })
    },
    createOrUpdateClassroom({ data, type }) {
      if (type === 'add') {
        this.createClassroom(data)
      } else {
        this.rename(data)
      }
    },
    create(type, val) {
      this.createDialogData = {
        visible: true,
        data: type === 'add' ? {} : val.row,
        type,
      }
    },
    resetPassword(rowArr) {
      if (!rowArr.length) {
        return this.$message({
          type: 'warning',
          message: '没有选中任何教室',
        })
      }
      const url = '/pad/j/classrooms/reset_password'
      const pks = rowArr.map(ele => ele.id)
      axios.post(url, qs.stringify(pks)).then(response => {
        if (response.status === 0) {
          return this.$message({
            type: 'success',
            message: '已重置所教室的密码',
          })
        }
        return null
      })
      return null
    },
    searchHandle() {
      const { searchContent } = this
      if (searchContent.length) {
        const filterFunc = ele => ele.name === searchContent
        this.tableData = this.tableData.filter(filterFunc)
      }
    },
    setting(rowArr) {
      if (!rowArr.length) {
        this.$message({
          type: 'warning',
          message: '没有选中任何内容',
        })
        return
      }
      this.settingDialogData = {
        visible: true,
        data: rowArr,
      }
    },
    selectHandle(val) {
      const condition = ['resetPassword', 'setting']
      this[condition[val]](this.selectOptions)
    },
    close({ type }) {
      this[`${type}DialogData`].visible = false
    },
    pageChangeHandle(index) {
      this.pageIndex = index
      this.getClassrooms()
    },
    batchSet(cb) {
      const pks = cb.fields.map(ele => ele.id)
      const {
        timing,
        phoneControl,
        timingStart,
        timingEnd,
        disablePhoneStart,
        disablePhoneEnd,
      } = cb.data
      const url = '/pad/j/classrooms/settings'
      const params = {
        id: pks,
        disablePhoneStart,
        disablePhoneEnd,
        timing,
        timingStart,
        timingEnd,
        phoneControl,
      }
      axios
        .post(url, qs.stringify(params, { arrayFormat: 'repeat' }))
        .then(response => {
          if (response.data.status === 0) {
            this.settingDialogData.visible = false
            this.getClassrooms()
            return this.$message({
              type: 'success',
              message: '设置成功',
            })
          }
          return this.$message({
            type: 'warning',
            message: '设置失败',
          })
        })
    },
  },
}
</script>

<style lang="less" scoped>
.el-pagination {
  margin: 0;
  margin-top: 15px;
}
.el-table {
  td {
    .cell {
      button {
        padding: 0;
      }
    }
  }
}
.table-before {
  > div {
    &:first-child {
      font-weight: bold;
      font-size: 1.1rem;
    }
    &:last-child {
      width: 100%;
      display: flex;
      margin-top: 5px;
      div {
        display: inline-block;
      }
      > div {
        margin-right: 2rem;
        width: 9rem;
      }
      div {
        &:first-child {
          span {
            font-weight: bold;
          }
        }
        &:last-child {
          display: flex;
          align-items: center;
        }
      }
      .el-switch {
        margin-left: 0.5rem;
      }
    }
  }
}
.batch-select {
  width: 110px;
}
</style>



