<template>
  <el-dialog :visible="visible" title="查看详情" fullscreen>
    <div class="container" v-for="(item, key, index) in detailData" :key="index">
      <h3>{{`${dict[key]}（${item.length}）`}}</h3>
      <div class="headContainer">
        <div class="head" v-for="({src, name}, cIndex) in item" :key="cIndex">
          <div class="img">{{src}}</div>
          <div class="name">{{name}}</div>
        </div>
      </div>
    </div>
  </el-dialog>
</template>

<script>
const dict = {
  absent: '缺席',
  late: '迟到',
  leave: '请假',
  normal: '正常',
}

export default {
  props: {
    visible: { type: Boolean },
    data: { type: Object },
  },
  data() {
    return {
      dict,
      detailData: {
        absent: new Array(20).fill({ src: '', name: '张三' }),
        late: new Array(10).fill({ src: '', name: '张三' }),
        leave: new Array(0).fill({ src: '', name: '张三' }),
        normal: new Array(50).fill({ src: '', name: '张三' }),
      },
    }
  },
  methods: {
    close() {
      this.$emit('close', { type: 'course' })
    },
  },
  mounted() {
    console.log(this.data)
  },
}
</script>

<style lang="less" scoped>
.headContainer {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
  &::after {
    content: '';
    flex-grow: 1;
  }
}
.head {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-shrink: 0;
  flex-grow: 0;
  margin: 5px 10px;
  .img {
    width: 80px;
    height: 80px;
    background: #66ccff;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
  }
}
</style>



