<template>
  <div>
    <el-card>
      <main-header slot="header" title="志愿调研">
        <el-button slot="right" type="primary" @click="createResearch()">新建志愿调研</el-button>
      </main-header>
      <div class="table-before">
        <div>
          <label>状态：</label>
          <el-select v-model="statusVal" :value="0" size="small" @change="selectHandle">
            <el-option v-for="item in statusOptions" :key="item.value" v-bind="item" />
          </el-select>
          <label>开始时间：</label>
          <el-select v-model="timeVal" :value="0" size="small" @change="selectHandle">
            <el-option v-for="item in timeOptions" :key="item.value" v-bind="item" />
          </el-select>
        </div>
        <el-input placeholder="搜索调研名称" v-model="searchContent" size="small" style="width:200px">
          <el-button slot="append" icon="el-icon-search" @click="searchHandle" />
        </el-input>
      </div>
      <el-table :data="researchs" stripe style="width: 100%">
        <el-table-column align="center" v-for="(item, index) in columns" :key="index" v-bind="item">

          <template slot-scope="{row,column,$index}">
            <span v-if="item.prop">
              {{row[column.property]}}
            </span>
            <template v-else>
              <div v-if="item.label==='时间' ">
                <span>{{ row.startTime }}</span><br>
                <span>至</span><br>
                <span>{{ row.endTime }}</span>
              </div>
              <div v-if="item.label==='操作' ">
                <el-button v-if="judgeStatus(row,'未开始')" type="text">开始</el-button>
                <el-button v-if="judgeStatus(row,'进行中')" type="text">结束</el-button>
                <el-button v-if="judgeStatus(row,'未开始','进行中')" type="text">编辑</el-button>
                <el-button v-if="judgeStatus(row,'已结束')" type="text">重新开始</el-button>
                <el-button v-if="judgeStatus(row,'未开始','进行中','已结束')" type="text" @click="analyze(row)">分析</el-button>
                <el-button v-if="judgeStatus(row,'未开始','已结束')" type="text">删除</el-button>
              </div>
            </template>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination layout="prev, pager, next, jumper " :total="total " :page-size="20 " @current-change="pageChangeHandle " />
    </el-card>
  </div>
</template>

<script>
const statusOptions = [
  { label: '未开始', value: 0 },
  { label: '已开始', value: 1 },
  { label: '已结束', value: 2 },
]
const timeOptions = [
  { label: '2018', value: 0 },
  { label: '2017', value: 1 },
]
const columns = [
  { label: 'ID', prop: 'id' },
  { label: '调研名称', prop: 'name' },
  { label: '时间' },
  { label: '状态', prop: 'status' },
  { label: '进度', prop: 'progress' },
  { label: '操作' },
]
export default {
  data() {
    return {
      searchContent: '',
      pageIndex: 1,
      total: 1,
      researchs: [],
      statusVal: 0,
      timeVal: 0,
      statusOptions,
      timeOptions,
      columns,
    }
  },
  methods: {
    createResearch() {
      console.log('create')
    },
    pageChangeHandle(pageIndex) {
      this.pageIndex = pageIndex
    },
    searchHandle(text) {
      console.log(text)
    },
    selectHandle() {
      console.log(this.statusVal, this.timeVal)
    },
    analyze() {
      this.$router.push('/volunteer/analyze')
    },
    judgeStatus({ status }, ...condition) {
      return condition.find(ele => ele === status)
    },
  },
  mounted() {
    this.researchs = [
      {
        id: 1,
        name: 2,
        startTime: '123',
        endTime: '456',
        status: '未开始',
      }, {
        id: 2,
        name: 2,
        startTime: '123',
        endTime: '456',
        status: '进行中',
      }, {
        id: 3,
        name: 2,
        startTime: '123',
        endTime: '456',
        status: '已结束',
      },
    ]
  },

}
</script>

<style lang="less" scoped>
.el-pagination {
  margin-top: 20px;
}
.el-table {
  td {
    .cell {
      button {
        padding: 0;
      }
    }
  }
}
.table-before {
  > div {
    flex-grow: 0;
    flex-shrink: 0;
  }
}
</style>



