<template>
  <div id="sidebar">
    <el-menu :default-active="defaultActive" router @open="open" unique-opened>
      <template v-for="(item, index) in items">
        <el-menu-item v-if="!item.children" :index="index+''" :key="index" :route="{path:item.path}">
          {{item.text}}
        </el-menu-item>
        <el-submenu v-if="item.children" :index="item.path" :key="index">
          <template slot="title">
            <span>{{item.text}}</span>
          </template>
          <el-menu-item v-for="(cItem, cIndex) in item.children" :index="index+'-'+cIndex+''" :key="cIndex" :route="{path:cItem.path}">
            {{cItem.text}}
          </el-menu-item>
        </el-submenu>
      </template>
    </el-menu>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [
        // { path: '/classroom', text: '班牌' },
        // { path: '/exam', text: '成绩' },
        // {
        //   path: '/attendance/student',
        //   text: '考勤管理',
        //   children: [
        //     { path: '/attendance/student', text: '学生考勤详情' },
        //     { path: '/attendance/course', text: '课程考勤详情' },
        //   ],
        // },
        // {
        //   path: '/volunteer',
        //   text: '志愿调研',
        //   children: [
        //     { path: '/volunteer/create', text: '新建志愿调研' },
        //     { path: '/volunteer/analyze', text: '志愿调研分析' },
        //   ],
        // },
        { path: '/message', text: '消息' },
        { path: '/message/contacts', text: '通讯录' },
      ],
    }
  },
  computed: {
    defaultActive() {
      const { path } = this.$route
      const activeIndex = this.items.reduce((acc, ele, index) => {
        if (ele.path === path || (ele.children && ele.children.find(cele => cele.path === path))) return index
        return acc
      }, 0)
      return `${activeIndex}`
    },
  },
  methods: {
    open(index) {
      this.$router.push(index)
      this['default-active'] = index
    },
  },
}
</script>


<style lang="less" scoped>
.is-active {
  outline: 0;
  background-color: #ecf5ff;
}
.el-menu {
  border: none;
}
</style>



