<template>
  <div id="navContainer">
    <router-link v-for="({path, text}, index) in options" :to="path" :key="index" class="aaa">
      {{text}}
    </router-link>
  </div>
</template>

<script>

export default {
  data() {
    return {
      options: [
        { path: '/message/chat', text: '消息' },
        { path: '/message/contacts', text: '通讯录' },
      ],
    }
  },
}
</script>


<style lang="less" scoped>
#navContainer {
  height: 100%;
  width: 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #ddd;
  flex-shrink: 0;
  > a {
    text-align: center;
    margin-left: 20px;
    padding: 15px;
    color: #000;
    text-decoration: none;
    display: block;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
  }
  .active {
    background: #fff;
  }
}
</style>




