<template>
  <el-dialog title="设置" @open="open" @close="close" :visible="visible">
    <el-form :model="form">
      <el-form-item label="定时开关机：">
        <el-radio v-model="form.timing" :label="1">启用</el-radio>
        <el-radio v-model="form.timing" :label="0">关闭</el-radio>
      </el-form-item>
      <el-form-item label="开关机时间：">
        <el-time-select :disabled="!form.timing" v-model="form.timingStart" :picker-options="timeSelectOptions" placeholder="选择开机时间" />
        <el-time-select :disabled="!form.timing" v-model="form.timingEnd" :picker-options="timeSelectOptions" placeholder="选择关机时间" />
      </el-form-item>
      <el-form-item label="电话管控：">
        <el-radio v-model="form.phoneControl" :label="1">启用</el-radio>
        <el-radio v-model="form.phoneControl" :label="0">关闭</el-radio>
      </el-form-item>
      <el-form-item label="时间范围：">
        <el-time-select :disabled="!form.phoneControl" :picker-options="timeSelectOptions" v-model="form.disablePhoneStart" />
        <el-time-select :disabled="!form.phoneControl" :picker-options="timeSelectOptions" v-model="form.disablePhoneEnd" />
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
const initData = {
  timing: 0,
  phoneControl: 0,
  timingStart: '06:00',
  timingEnd: '22:00',
  disablePhoneStart: '06:00',
  disablePhoneEnd: '22:00',
}
export default {
  data() {
    return {
      timeSelectOptions: {
        start: '00:00',
        step: '00:05',
        end: '23:55',
      },
      form: initData,
    }
  },
  props: {
    visible: { type: Boolean },
    data: { type: Array },
  },
  methods: {
    open() {
      this.form = this.data.length === 1 ? { ...this.data[0] } : initData
    },
    close() {
      this.$emit('close', { type: 'setting' })
    },
    confirm() {
      if (
        this.form.timing === 1 &&
        (this.form.timingStart === '' || this.form.timingEnd === '')
      ) {
        this.$message({
          type: 'warning',
          message: '定时开关机时间不能为空',
        })
        return
      }
      if (
        this.form.phoneControl === 1 &&
        (this.form.disablePhoneStart === '' || this.form.disablePhoneEnd === '')
      ) {
        this.$message({
          type: 'warning',
          message: '电话管控时间范围不能为空',
        })
        return
      }
      this.$emit('confirm', { fields: this.data, data: this.form })
      this.close()
    },
  },
}
</script>



