const pickerShortcutClick = (picker, day) => {
  const end = new Date()
  const start = new Date()
  const ms = 3600 * 1000 * 24 * day
  start.setTime(start.getTime() - ms)
  picker.$emit('pick', [start, end])
}

export const studentInfoColumns = [
  { label: '学生姓名', prop: 'name' },
  { label: '正常', prop: 'normol' },
  { label: '缺席', prop: 'absent' },
  { label: '迟到', prop: 'late' },
  { label: '请假', prop: 'leave' },
]

export const pickerOptions = {
  shortcuts: [{
    text: '最近一周',
    onClick(picker) {
      pickerShortcutClick(picker, 7)
    },
  }, {
    text: '最近一个月',
    onClick(picker) {
      pickerShortcutClick(picker, 30)
    },
  }, {
    text: '最近三个月',
    onClick(picker) {
      pickerShortcutClick(picker, 90)
    },
  }, {
    text: '最近一年',
    onClick(picker) {
      pickerShortcutClick(picker, 365)
    },
  },
  ],
}

export const attendanceInfo = [{
  label: '正常',
  key: 'normol',
  color: '#67C23A',
}, {
  label: '缺席',
  key: 'absent',
  color: '#F56C6C',
}, {
  label: '迟到',
  key: 'late',
  color: '#409EFF',
}, {
  label: '请假',
  key: 'leave',
  color: '#909399',
},
]

export const chartOptions = {
  title: {
    text: '出勤率',
    left: 'center',
    textStyle: {
      fontWeight: 'normal',
    },
  },
  series: [{
    name: '考勤',
    type: 'pie',
    data: [{ value: 20, name: '未出勤' }, { value: 80, name: '出勤' }],
    label: {
      normal: {
        show: false,
      },
    },
    labelLine: {
      normal: {
        show: false,
      },
    },
  },
  ],
  color: ['#555', '#409EFF'],
}



