<template>
  <el-card id="exam-create">
    <main-header slot="header" title="新建考试" />
    <el-form>
      <el-form-item label="班级：" required>
        <el-input v-model="className" />
      </el-form-item>
      <el-form-item label="学科：">
        <el-checkbox-group v-model="checkList">
          <el-checkbox v-for="(item, key) in optionList" :key="'s-'+key" :label="item" />
          <el-button type="text" @click="otherSubjectShow = !otherSubjectShow">
            <span>{{otherSubjectShow ? '收起' : '其他学科 >>'}}</span>
          </el-button>
          <div v-if="otherSubjectShow">
            <el-checkbox v-for="(item, key) in otherSubjectList" :key="optionList.length + key" :label="item" />
          </div>
        </el-checkbox-group>
      </el-form-item>
    </el-form>
    <el-table :row-key="row => `${row.id}`" @filter-change="classFilter" :data="tableData" stripe style="width: 100%" @selection-change="selectChange">
      <el-table-column type="selection" width="40" reserve-selection/>
      <el-table-column prop="id" label="班级名称" align="center" />
      <el-table-column column-key="grade" prop="grade" :filters="[{text:'高一',value:22}]" label="年级 " align="center" />
      <el-table-column column-key="teacher" prop="teacher" :filters="[{text:'张三',value:22}]" label="班主任 " align="center" />
      <el-table-column prop="number" label="学生数" align="center" />
      <el-table-column :render-header="renderHeader" width="220" />
    </el-table>
    <el-pagination layout="prev, pager, next, jumper" :total="total" :page-size="20" @current-change="pageChangeHandle" />
    <el-button type="primary" @click="submitHandle">完成</el-button>
  </el-card>
</template>

<script>
export default {
  props: ['visible', 'close'],
  data() {
    return {
      className: '',
      filterObj: {},
      tableSelectOptions: [],
      pageIndex: 1,
      otherSubjectShow: false,
      searchContent: '',
      optionList: [
        '语文',
        '数学',
        '物理',
        '英语',
        '化学',
        '生物',
        '政治',
        '地理',
        '历史',
      ],
      otherSubjectList: [
        '音乐',
        '体育',
        '美术',
        '通用技术',
        '科学',
        '信息技术',
      ],
      checkList: [],
      tableData: [
        {
          id: 1,
          name: '32',
          number: 'dsf',
          grade: '高一',
          teacher: '张三',
        },
        {
          id: 2,
          name: '33',
          number: 'dsf',
          grade: '高二',
          teacher: '张四',
        },
      ],
      total: 30,
    }
  },
  methods: {
    renderHeader(createElement) {
      return createElement(
        'el-input',
        {
          attrs: {
            placeholder: '搜索',
            size: 'small',
          },
          on: {
            input: val => {
              this.searchContent = val
            },
          },
        },
        [
          createElement('el-button', {
            slot: 'append',
            attrs: {
              icon: 'el-icon-search',
            },
            on: {
              click: this.searchHandle,
            },
          }),
        ]
      )
    },

    searchHandle() {
      console.log(this.searchContent.trim())
    },
    pageChangeHandle(index) {
      this.pageIndex = index
      //getTableData
    },
    selectChange(selection) {
      this.tableSelectOptions = selection
    },
    classFilter(filterOption) {
      const { filterObj } = this
      this.filterObj = { ...filterObj, ...filterOption }
      console.log(this.filterObj)
    },
    message(text) {
      this.$message({ message: text, type: 'error' })
    },
    submitHandle() {
      const { className, checkList, tableSelectOptions } = this
      if (!className.trim().length) {
        return this.message('未填写考试名称')
      }
      if (!checkList.length) {
        return this.message('未选择科目')
      }
      if (!tableSelectOptions.length) {
        return this.message('未选择班级')
      }
      return console.log(className, checkList, tableSelectOptions)
      //submit
    },
    mounted() {
      const { query } = this.$route
      if (query.id) {
        //init model
      }
    },
  },
}
</script>

<style lang="less">
#exam-create {
  .el-table {
    .el-form-item {
      label {
        width: 5rem;
      }
    }
    .el-input {
      display: flex;
      width: 200px;
    }
    .el-input-group__append {
      line-height: initial;
      padding: 0 20px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}
</style>



