import axios, { defaults } from 'axios';


import Vue from 'vue'
import VueRouter from 'vue-router'
import Toasted from 'vue-toasted'
import Navbar from 'components/navbar'
import Raven from 'raven-js'
// import RavenVue from 'raven-js/plugins/vue'
import {
  Menu, MenuItem, Button, Input, Table, TableColumn, Main, Aside, Container, Card, Dialog,
  Form, FormItem, Select, Option, OptionGroup, Radio, DatePicker, TimePicker, MenuItemGroup,
  RadioGroup, Message, Checkbox, CheckboxGroup, Switch, Tooltip, Pagination, TimeSelect, Dropdown, DropdownItem,
  Submenu, DropdownMenu, Upload, Progress, Badge, Tag, Tabs, TabPane, Tree, Loading,
} from 'element-ui'
import '../../theme/element-variables.scss'

import SecondaryNavbar from 'components/manage/secondary-navbar'
import Sidebar from 'components/manage/sidebar'
import MainHeader from 'components/manage/main-header'

import './index.less'

import router from './route'
import store from './store/'

window.Promise = require('bluebird')

Vue.config.productionTip = false

defaults.headers.common.X_REQUESTED_WITH = 'XMLHttpRequest'
defaults.xsrfHeaderName = 'X-CSRFTOKEN'
defaults.xsrfCookieName = 'csrftoken'

Vue.prototype.$raven = Raven
// if (process.env.NODE_ENV === 'production') {
//   Raven.config('http://216e3d581cd9424fad7f5d8c046b2f64@sentry.xdf100.com/6').addPlugin(RavenVue, Vue).install()
// }

Vue.component('main-header', MainHeader)
Vue.use(VueRouter)
Vue.use(Toasted)
Vue.use(Menu)
Vue.use(MenuItem)
Vue.use(Button)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Main)
Vue.use(Aside)
Vue.use(Container)
Vue.use(Card)
Vue.use(Dialog)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Select)
Vue.use(Option)
Vue.use(OptionGroup)
Vue.use(Input)
Vue.use(Radio)
Vue.use(TimeSelect)
Vue.use(MenuItemGroup)
Vue.use(RadioGroup)
Vue.use(Checkbox)
Vue.use(CheckboxGroup)
Vue.use(Switch)
Vue.use(Tooltip)
Vue.use(Pagination)
Vue.use(Dropdown)
Vue.use(DropdownItem)
Vue.use(DropdownMenu)
Vue.use(Submenu)
Vue.use(Upload)
Vue.use(Progress)
Vue.use(DatePicker)
Vue.use(Badge)
Vue.use(Tag)
Vue.use(Tabs)
Vue.use(TabPane)
Vue.use(Tree)
Vue.use(Loading.directive)
Vue.prototype.$message = Message

router.beforeEach((to, from, next) => {
  if (!store.state.teacher) {
    axios.get('/pad/j/teacher_info').then(response => {
      const { teacher, accessToken, enableMessage } = response.data
      store.commit('updateAccessToken', { accessToken: `Bearer ${accessToken}` })
      store.commit('updateTeacher', { teacher })
      store.commit('updateEnableMessage', { enableMessage })
      if (process.env.NODE_ENV === 'production') {
        Raven.setUserContext({
          username: teacher.name,
          id: teacher.id,
        })
      }
      next()
    })
  } else {
    next()
  }
})

new Vue({
  delimiters: ['${', '}'],
  el: '#app',
  store,
  router,
  data: {},
  components: { Navbar, SecondaryNavbar, Sidebar },
})



