<template>
  <el-card id="exam-create">
    <main-header slot="header" :title="name">
      <div slot="right">
        <el-upload :action="action" :on-success="uploadSuccess" :on-error="uploadError" :on-progress="uploadProgress" :multiple="false" :with-credentials="true" :show-file-list="false">
          <el-button type="primary">点击导入</el-button>
        </el-upload>
      </div>
    </main-header>

    <el-table :data="scoreInfo" stripe style="width: 100%">
      <el-table-column v-for="(val, key, index) in scoreInfo[0]" :label="key" :prop="key" align="center" :key="index" />
    </el-table>
    <el-pagination layout="prev, pager, next, jumper" :total="total" :page-size="20" @current-change="pageChangeHandle" />
  </el-card>
</template>

<script>
export default {
  props: ['visible', 'close'],
  data() {
    return {
      pageIndex: 1,
      total: 1,
      action: 'https://www.google.com',
      scoreInfo: [
        {
          id: 1,
          math: 90,
          english: 78,
          science: 67,
          computer: 28,
        },
      ],
    }
  },
  computed: {
    id() {
      return this.$route.query.id
    },
    name() {
      return this.$route.query.name
    },
  },
  methods: {
    pageChangeHandle(index) {
      this.pageIndex = index
      console.log(index)
    },
    uploadSuccess(res) {
      console.log(res)
      //rerender
      this.$message({
        type: 'success',
        message: '导入成功',
      })
    },
    uploadError(error) {
      console.log(error)
      this.$message({
        type: 'error',
        message: '导入失败',
      })
    },
    uploadProgress(event) {},
  },
  mounted() {
    //get scoreInfo
  },
}
</script>

