<template>
  <nav class="main-top-nav" v-if="teacher">
    <div class="ui blue inverted borderless menu">
      <div class="item logo" @click="gotoHomeUrl">
        <img class="ui tiny image" src="~assets/img/brand-logo.png">
      </div>
      <template v-if="teacher.isCourseTeacher">
        <a class="item" :href="pathConvert('resource', 'mine')" :class="activeTab==='resource'? 'active': ''">备课</a>
        <a class="item" :class="activeTab==='question-bank'? 'active': ''" :href="pathConvert('question-bank', 'gallery')" v-if="displayNewQuestionBank">新题库</a>
        <a class="item" :href="pathConvert('teaching')" :class="activeTab==='teach'? 'active': ''">上课</a>
        <a class="item" :href="pathConvert('course-material')" :class="activeTab==='course-material'? 'active': ''">作业批改</a>
      </template>
      <a class="item" :href="manageDefaultHref" v-if="teacher.isManager || teacher.isHeadTeacher || teacher.isCourseTeacher" :class="activeTab==='management' && 'active'">管理</a>
      <a class="item chat-item" v-if="teacher.isHeadTeacher || teacher.isCourseTeacher" :href="pathConvert('classroom-manage', 'message/chat')" :class="activeTab==='classroom-manage'? 'active': ''">
        聊天
        <i v-if="unread > 0 && activeTab !== 'classroom-manage'">{{unread}}</i>
      </a>
      <a class="item" :href="pathConvert('data-analyze')" :class="activeTab==='data-analyze'? 'active': ''">数据分析</a>
      <div class="right menu">
        <a class="item" :href="pathConvert('notice')" title="通知" :class="activeTab==='notice'? 'active': ''">
          <i class="alarm outline icon"></i>
          通知
        </a>
        <a class="item" :href="pathConvert('support', 'summary.md')" title="使用帮助">
          <i class="help circle icon"></i>
          使用帮助
        </a>
        <div class="ui dropdown item">
          {{teacher.school.name}} / {{teacher.username}}
          <i class="dropdown icon"></i>
          <div class="menu">
            <a class="item" :href="pathConvert('dashboard')">个人主页</a>
            <a class="item" href="/pad/fetch_change_pwd_code">修改密码</a>
            <a class="item" href="/pad/logout">登出</a>
          </div>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import { NEW_BANK_ACCESSABLE_SUBJECT_IDS } from '@/assets/js/consts/common'
import { pathConvert } from '@/assets/js/utils'

// Array.prototype.includes 兼容
import '@/polyfill/'

export default {
  name: 'main-navbar',
  data() {
    return {
      pathConvert,
      unread: 0,
    }
  },
  props: {
    activeTab: { type: String },
  },
  async mounted() {
    this.$nextTick(() => $('.main-top-nav .ui.dropdown').dropdown())
    const { data: { accessToken } } = await axios.get('/pad/j/teacher_info')
    const { data: { count } } = await axios.get('/teacher/api/chats/unread', { headers: { Authorization: `Bearer ${accessToken}` } })
    this.unread = count
  },
  watch: {
    teacher() {
      this.$nextTick(() => $('.main-top-nav .ui.dropdown').dropdown())
    },
  },
  computed: {
    displayNewQuestionBank() {
      if (!this.teacher) return false
      return NEW_BANK_ACCESSABLE_SUBJECT_IDS.includes(this.teacher.subjectId)
    },
    manageDefaultHref() {
      const { isManager, isHeadTeacher } = this.teacher
      if (isManager || isHeadTeacher) return this.pathConvert('management', 'classes')
      return '/pad/manage/courses'
    },
    ...mapState(['teacher']),
  },
  methods: {
    gotoHomeUrl() {
      window.location.href = '/'
    },
  },
}
</script>

<style lang="less">
@import "~assets/css/base-variables.less";
.main-top-nav {
  background-color: @color-primary;
  .ui.menu {
    font-size: 14px;
    border-radius: 0;
    height: 50px;
    .ui.dropdown {
      .menu > .header {
        font-size: 1em;
      }
      .menu > .item {
        font-size: 0.95em !important;
        color: @font-color-base-light !important;
      }
    }
    .item.logo {
      cursor: pointer;
    }
    .item {
      padding-left: 1rem;
      padding-right: 1rem;
    }
    .item:first-child {
      padding-left: 0;
    }
    .item:last-child {
      padding-right: 0;
    }
    .item .icon {
      padding: 0 0.5rem;
      margin: 0;
    }
    .item .help.icon {
      padding: 0 0.5rem;
      margin-right: 0.5rem;
    }
    .item .alarm {
      padding: 0 0.5rem;
      margin-right: 0.5rem;
      font-size: 12px;
    }
  }
  .chat-item {
    position: relative;
    i {
      position: absolute;
      right: -4px;
      top: 0;
      min-width: 16px;
      height: 16px;
      border-radius: 8px;
      background: red;
      color: white;
      font-style: normal;
      font-size: 13px;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 3px;
      z-index: 1;
    }
  }
}
</style>



