<template>
  <el-dialog @open="open" @close="close" :title="type === 'add' ? '新建教室':'编辑教室'" :visible="visible" width="700px">
    <el-form :model="classroom">
      <el-form-item label="教室名称：" style="display: flex">
        <el-input v-model="classroom.name" auto-complete="off" />
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  data() {
    return {
      classroom: {},
    }
  },
  props: {
    visible: { type: Boolean },
    data: { type: Object },
    type: { type: String },
  },
  methods: {
    open() {
      this.classroom = { ...this.data }
    },
    close() {
      this.$emit('close', { type: 'create' })
    },
    confirm() {
      const { name } = this.classroom
      if (name === undefined || name.length === 0) {
        this.$message({
          type: 'error',
          message: '教室名称不能为空',
        })
        return
      }
      this.$emit('confirm', { data: this.classroom, type: this.type })
      this.close()
    },
  },
}
</script>



